@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */

.navbar{
  font-family: 'Poppins', sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
}

.navbar-brand{
    font-size: 20px;
    font-weight:bold;
    font-family:"roboto",sans-serif;
    letter-spacing: 2px;
}

.nav-link{
  font-weight: 300 !important;
}

.active{
  border-bottom: 2px solid black;
}


/* footer   */
.footer{
  color: white;
  padding: 10px;
  background-color: black;
  height: 12vh;
}
.footer a{
  text-decoration: none;
  color: rgb(230, 212, 212);
  padding:4px;
}

.footer a:hover{
  color: rgb(230, 202, 168);
  border-bottom: 1px solid snow;
}

/* Page not Found */
.pnf{
  display: flex;
  min-height: 68vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.pnf-err{
  font-size: 40px;
  font-weight: 500;
}
.pnf-txt{
  font-size: 28px;
}
.pnf-btn{
  font-size: 20px;
  font-weight:bolder;
  text-decoration: none;
  border: 2px solid rgb(80, 77, 77);
  padding: 6px;
  padding-top: 2px;
  border-radius: 12px;
  background-color: rgb(203, 203, 194);
}


/* Register Page */
.register{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70vh;
}

/* Contact Page */
.contactus {
  margin: 0;
  padding: 0;
  height: 74vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contactus img{
  width: 80%;
  height: 45vh;
  border-radius: 25px;
}

/* About Page */
.about{
  margin: 0;
  padding: 0;
  height: 74vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.about img{
  width: 80%;
  height: 45vh;
  border-radius: 25px;
}

/* Policy Page */
.policy{
  margin: 0;
  padding: 0;
  height: 74vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.policy img{
  width: 80%;
  height: 45vh;
