/* Register Page, Login Page*/
.form-container{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 79vh;
    background-color: rgb(195, 214, 208);
    background-image: linear-gradient(0deg, #e5dbde 0%, #c0e2e1 100%);
}

.form-container form {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 20px;
  background-color: #b09292;
  border-radius: 8px;
}

.form-container .title{
    text-align: center;
    margin-bottom: 15px;
    font-weight: bold;
    font-family: "Playfair Display", serif;
    color: rgb(22, 20, 20);
}

.form-container button{
    border: 2px solid rgb(86, 84, 84);
    color: aliceblue;
    background-color: rgb(63, 63, 64);
    width: 100%;
    font-size: 17px;
}

.form-container button:hover{
    background-color: rgb(72, 72, 146);
    width: 100%;
    font-size: 17px;
}
